html {
  position: relative;
  min-height: 100%;
  width: 100%; }

.footer {
  background: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0)), url("../../assets/Images/footer1.jpg");
  background-size: 100% 100%;
  position: relative;
  font-size: 1rem;
  width: 100%;
  bottom: 0; }
  .footer .container {
    position: relative;
    word-wrap: break-word;
    text-align: center;
    text-shadow: 0px 0px 5px #131313, 0px 0px 5px #070707, 0px 0px 5px black, 0px 0px 5px black; }

.mainLinks li {
  margin-right: 20px;
  display: inline; }
  .mainLinks li a:hover {
    color: #202020;
    text-shadow: 0px 0px 3px #ff0000, 0px 0px 3px #ff0000, 0px 0px 3px red, 0px 0px 3px red; }

.socialIcons {
  margin-top: -5px;
  display: inline-block;
  height: 0;
  text-align: center; }
  .socialIcons li {
    font-size: 20px;
    display: inline; }
    .socialIcons li a {
      display: inline-block;
      font-size: 2.5rem;
      width: 80px;
      line-height: 50px;
      color: #fff5b2;
      transition: all 0.5s ease;
      transform-origin: left;
      transform: rotateY(-30deg);
      text-shadow: 10px 0px 1px #131313, 10px 0px 1px #070707, 10px 0px 1px black, 10px 0px 1px black; }
      .socialIcons li a:hover {
        color: #202020;
        transform: rotate(0deg);
        text-shadow: 0px 0px 1px #131313, 0px 0px 1px #070707, 0px 0px 1px black, 0px 0px 1px black; }

.text-white {
  margin-bottom: 0; }
